import React from 'react'
import { convertCss } from 'css-to-typestyle'

class Home extends React.Component {
    constructor(props){
        super(props)
        this.state = {
            css:`body {
                    font-size:14px;
                }`,
            typestyle:`body {
                    fontSize:14px;
                }`
        }
        this.handleChange = this.handleChange.bind(this)
    }
    async handleChange(v){
        this.setState({
            css:v.target.value
        })
        try {
            this.setState({
                typestyle:await convertCss(`${v.target.value}`)
            })
        }catch(error){
            this.setState({
                typestyle:"Incorrect css code!"
            })
        }

    }
    render(){
        return (
            <div className="container-fluid">
                <div className="row">
                    <div className="col">
                        <div className="form-group">
                            <label htmlFor="exampleFormControlTextarea1">Css Code</label>
                            <textarea onChange={v=>this.handleChange(v)}className="form-control" id="exampleFormControlTextarea1" rows="20" value={this.state.css}></textarea>
                        </div>
                    </div>
                    <div className="col">
                        <div className="form-group">
                            <label htmlFor="exampleFormControlTextarea1">typestyle Code</label>
                            <textarea className="form-control" id="exampleFormControlTextarea1" rows="20" readOnly value={this.state.typestyle}></textarea>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}

export default Home
